import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
// import { Api } from '../enums/api.enum';
import { Observable } from 'rxjs';
import { AppConfig } from 'src/app/config/app-config';

@Injectable({
  providedIn: 'root'
})
export class RulesService {

  constructor(private http: HttpClient) { }

   getFieldValuesData(onloadjson){
    return this.http.post<any>(AppConfig.endPoint + 'Fieldvalues' ,onloadjson);
  }

  getExistingData(username){
    return this.http.post<any>(AppConfig.endPoint + 'ExistingDetails', username);
  } 

  parseXLSX(data){
    return this.http.post(AppConfig.endPoint + 'Excelreturn',data)
  }

  saveData(data){
    return this.http.post<any>(AppConfig.endPoint + 'Savedrl',data);
  }
  delete(data){
    return this.http.post<any>(AppConfig.endPoint + 'DeleteApi',data);
  }
  uploadExcel(data){
    return this.http.post<any>(AppConfig.endPoint + 'ExcelFields',data);
  }
  downLoadExcel(data){
    return this.http.post<any>(AppConfig.endPoint + 'DownloadExcel',data);
  }
  uploadExcelStep1(data){
    return this.http.post<any>(AppConfig.endPoint + 'TransformUploadExcel',data);
  }
  downoadExcelStep1(data){
    return this.http.post<any>(AppConfig.endPoint + 'TransformDownload',data);
  }
  downoadPossibleExcel(data){
    return this.http.post<any>(AppConfig.endPoint + 'DownloadPossible',data);
  }

  getExistingOutputBuilder(data){
    return this.http.post<any>(AppConfig.endPoint + 'OutputBuilderExists', data);
  }
  createSplitter(data){
    return this.http.post<any>(AppConfig.endPoint + 'SplitterFields',data);
  }
  postConstructs(data){
    return this.http.post<any>(AppConfig.endPoint + 'Constructs',data);
  }
}
