 GetParam(name){
    const results = new RegExp('[\\?&]' + name + '=([^&#]*)').exec(window.location.href);
    if(!results){
		
      return 0;
    }
    return results[1] || 0;
  }


  loadExistingData() {
    this.showLoading = true;
    this.ruleservice.getExistingData({ username:this.GetParam('username'), projectname:  this.GetParam('projectname'),source: this.GetParam('source') }).subscribe(exisData => {
      if (exisData !== null && exisData !== undefined) {
        this.exsitingData = exisData.RuleJson ? exisData.RuleJson : [];
      } else {
        this.exsitingData = [];
      }
      this.showLoading = false;
      this.prepareDisplayData();
      // this.pane = 0;
      // this.allowAddRule = false;
      // this.allowEditRule = false;

      this.show_pane0();
    });

    this.ruleservice.getExistingOutputBuilder({ username: this.GetParam('username'), projectname:  this.GetParam('projectname'),source: this.GetParam('source')}).subscribe(responseData => {
      this.exsitingOutputBuilderData = responseData.Mainjson;
    });
  }

  prepareDisplayData() {

    var isSplitterAdded = false;

    this.exsitingData.forEach(element => {
      let header = []
      element.data.forEach(element1 => {
        let val = []
        element1.SFdata.forEach(element2 => {
          
          // for Category and its value
          if(element2.Category){
            let result = header.filter(obj => {
              return obj.field === element2.field && obj.Category === element2.Category
            });

            if (result.length <= 0) {
              header.push({
                field: element2.field,
                Category: element2.Category
              })
            }

            let indx = this.findWithAttr(header, 'field', 'Category', element2.field, element2.Category)
            
            if (indx >= 0) {
              val[indx] = element2.value;
            }
          }

          // for Category1 and its value1
          if(element2.Category1){
            let result1 = header.filter(obj => {
              return obj.field === element2.field && obj.Category === element2.Category1
            });

            if (result1.length <= 0) {
              header.push({
                field: element2.field,
                Category: element2.Category1
              })
            }

            let index1 = this.findWithAttr(header, 'field', 'Category', element2.field, element2.Category1)
            
            if (index1 >= 0) {
              val[index1] = element2.value1;
            }
          }

        });
        //value.push(val)
        element1.values = val;
      });
      element.headers = header;
      // isSplitterAdded = element.isSplitterAdded ? true : false;
    });

    this.exsitingData.forEach(element => {
      element.data.forEach(element1 => {
        if (element1.values.length < element.headers.length) {
          element1.values[element.headers.length - 1] = '';
        }
      });
    });

    this.isSplitterAdded = isSplitterAdded;

    this.savedLevelsData = [];
    if(this.exsitingData.length > 0){
      this.exsitingData.forEach(ruleEngine => {
        var isClusterExist = this.savedLevelsData.find(x => {
          if(x.clusterName === ruleEngine.LevelData){
            return true;
          }else{
            return false;
          }
        });

        if(!isClusterExist){
          var obj = {
            'clusterName' : ruleEngine.LevelData,
            'isSplitterAdded' : ruleEngine.isSplitterAdded ? ruleEngine.isSplitterAdded : false,
            'OutputBuilder' : ruleEngine.OutputBuilder ? ruleEngine.OutputBuilder : false,
            'selectedConstructs' : []
          };

         console.log("dataRule= "+this.dataRule);
	//console.log("dataRule cons= "+this.dataRule.Constructs);
	  if(this.dataRule["Constructs"]){
         this.dataRule.Constructs.forEach(construct => {
           obj[construct] = false;
         });
	  }
          this.savedLevelsData.push(obj);
        }
      });
    }

    console.log(this.exsitingData);
  }

  save() {
    this.submitted = true;

    if (this.entryCriteriaForm.invalid) {
      alert("Rule engine name and rule name are required fields.")
      return;
    }

    this.disableSetupruleButton = true;
    let ruleJsonAry = []
    if (this.pane == 1) {
      var received = this.entryCriteriaForm.value;

      let dataAry = []
      let fieldAry = [];
      let outPutFiledAry = []
      this.trigger_fields.forEach(element => {
        fieldAry.push({ "Field": element.Field, "type": element.type })
      });
      this.output_trigger_fields.forEach(element => {
        // outPutFiledAry.push(element.Field)
        outPutFiledAry.push({Field: element.Field, Value: element.Value});
      });
      // let rName = 'R1';
      // if (this.allowEditRule) {
      //   rName = this.selectedRuleEngine.data[this.selectedRuleIndex].rulename
      // } else if (this.allowAddRule) {
      //   rName = 'R' + new Date().getTime()
      // }
      received.data.forEach(element => {
        let engineName = received.ruleengine_name;
        element.rulename = element.rulename;
        let sfDataAry = []
        element.SFdata.forEach(element1 => {
          element1.symbol_category = element1.Category;
          // element1.type = this.getType(element1);
          element1.type = element1.type;
          
          console.log(element1);
          if(element1.type === 'Date'){
            if(element1.value){
              element1.value = new DatePipe('en-US').transform(element1.value, 'dd/MM/yyyy');
              console.log(element1.value);
            }
            if(element1.value1){
              element1.value1 = new DatePipe('en-US').transform(element1.value1, 'dd/MM/yyyy');
              console.log(element1.value1);
            }
          }

          sfDataAry.push(element1);
          // if (element1.Category1 != "" && (element1.type == 'Integer' || element1.type == 'Url')) {
          //   sfDataAry.push({
          //     field: element1.field,
          //     Category: element1.Category1,
          //     type: element1.type,
          //     value: element1.value1,
          //     ValidFrom: element1.validFrom,
          //     ValidUpto: element1.validUpto,
          //     symbol_category: element1.Category
          //   })
          // }
        });
        element.SFdata = sfDataAry;
        element.Field = fieldAry;
        element.OutPutFiled = outPutFiledAry;
        dataAry.push(element);
        var object = {
          user_name: received.user_name,
          project_name: received.project_name,
          ruleengine_name: engineName,
          description : received.description,
          LevelData: this.CurrentLevel,
          // Created_By : element.Created_By,
          data: dataAry
        }

        if(this.isSplitterAdded){
          object['isSplitterAdded'] = this.isSplitterAdded;
        }

        var onLoadJsonSave = this.dataRule;
        var keys = Object.keys(onLoadJsonSave);
        for (var k = 0; k < keys.length; k++) {
          var key = keys[k];
          if (key != "Mainjson" && key != "Level") {
            console.log(key, onLoadJsonSave[key]);
            object[key] = onLoadJsonSave[key];
            if (key == 'Transform') {
              delete this.dataRule[key]
            }
          }
        }

        object['allowDataTransform'] = this.allowStep1DataTransform;
        ruleJsonAry.push(object)
      });
    }
    let ruleJson = { "RuleJson": ruleJsonAry }
    console.log('====================================');
    console.log(ruleJson);
    console.log('====================================');
    //this.loadExistingData();
    this.ruleservice.saveData(ruleJson).subscribe(data => {

      this.loadExistingData();
    }, err => {
      if (this.pane == 1) {
        received.data.forEach(element => {
          element.rulename = received.ruleengine_name
        });
      }

      console.log(err);
      alert('Failed to save.' + err.message)
      this.disableSetupruleButton = false;
    });
  }

