  isCSVFile(file: any) { return file.name.endsWith(".xlsx"); }

  fileChangeListener($event: any): void {
    var text = [];
    var files = $event.srcElement.files;
    if (files && files.length > 0) {
      if (this.isCSVFile(files[0])) {
        var input = $event.target;
        let reader = new FileReader();
        reader.readAsDataURL(files[0]);
        reader.onload = (e) => {
          var data = reader.result.toString();
          var object = { username: this.user_name, ExcelData: data.substr(data.indexOf('base64,') + 7) }
          this.ruleservice.parseXLSX(object).subscribe(data => {
            console.log(data);
            this.trigger_fields = data['ExcelData']
            this.setExcelData(this.trigger_fields);

            this.fileReset();
            alert("File uploaded successfully.")
          },
            err => {
              console.log("ERROR while parsing xlsx: ", err);
              this.fileReset();
            })
        }
      } else {
        alert("Please import valid .xlsx file.");
        this.fileReset();
      }
    }
  }

